import React from 'react';
import styled from 'styled-components';

const CircleNumber = styled.span`
	width: 30px;
	height: 30px;
	text-align: center;
	color: white; 
	background-color: #F48024;
	display: grid;
	justify-items: center;
	align-items: center;
	border-radius: 50%;
`;

export default CircleNumber;


